import userMixin from '@/mixins/user.js';

export default {
    data() {
        return {
            transPage: 'all',
            transactionForm: 'Withdraw',
            bank: null,
            amount: null,
            recepient: null,
            slide: null,
        }
    },
    mixins: [userMixin],
    methods: {
        toggleTransPage(page) {
            this.transPage = page;
        },
        closeslide() {
            this.slide = null
        },
        openslide(val) {
            this.slide = val
        },
        truncate(input) {
            if (input.length > 10) {
                return input.substring(0, 10) + '...';
            }
            return input;
        },
    }
}
